import BaseApiService from "../BaseApiService";

const DEFAULT_PATH = "/custom/api/login";


class SourcingService extends BaseApiService {
  getDdeDetails = (uid:any) => {
    const url = `${DEFAULT_PATH}/sourcing/uid/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };
  updateDde = (obj: any) => {
    const url = `${DEFAULT_PATH}/dde/sourcing`;
    return this.makePutRequestWithAuth(url, obj);
  };
  saveQde = (obj: any) => {
    const url = `${DEFAULT_PATH}/qde/sourcing`;
    return this.makePostRequestWithAuth(url, obj);
  };
}

export default new SourcingService();
